.App {
  text-align: center;
  width: 100%;
  height: 50px;
  background-color: blue;
  border-radius: 50%;

}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

body{
  background: linear-gradient(to right,rgb(116, 210, 241), rgb(255, 255, 0));

}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
